import { combineReducers } from 'redux'; 

let first = [
	{nid: 1, lesson_name: "", cla_name: "1571", student_name: "", student_id: "2015127001"},
	{nid: 2, lesson_name: "c", cla_name: "1572", student_name: "", student_id: "2015127051"}
			];

// 
const students = (state=first, action) => {
	switch(action.type){
		case "GET_STUDENTS": 
			state = action.students;
			return state;
		default: return state;
	}
}

// 
const curStudents = (state=[], action) => {
	switch(action.type){
		case "SET_CUR_STUDENTS": 
			state = action.curStudents;
			return state;
		default: return state;
	}
}

// 
const lesson = (state='', action) => {
	switch(action.type){
		case "SELECT_LESSON": 
			state = action.lesson;
			return state;
		default: return state;
	}
}

// 
const cla = (state='1571', action) => {
	switch(action.type){
		case "SELECT_CLA": state = action.cla; return state;
		default: return state;
	}
}

// 
const defaultLesson = (state='true', action) => {
	switch(action.type){
		case "SET_DEFAULT_LESSEN": state = action.val; return state;
		default: return state;
	}
}

// 
const defaultCla = (state='true', action) => {
	switch(action.type){
		case "SET_DEFAULT_CLA": state = action.val; return state;
		default: return state;
	}
}

//  
const curProjects = (state=[], action) => {
	switch(action.type){
		case "GET_CUR_PROJECTS": 
			state = action.val;
			return state;
		default: return state;
	}
}

// 
const setProjects = (state=[], action) => {
	switch(action.type){
		case "SET_PROJECTS": 
			state = action.val;
			return state;
		default: return state;
	}
}

// 
const targetStudentProjects = (state=[], action) => {
	switch(action.type){
		case "TARGET_STUDENT_PROJECTS":
			state = action.val;
			return state;
		default: return state;
	}
}

// 
const targetStudent = (state='', action) => {
	switch(action.type){
		case "TARGET_STUDENT":
			state = action.val;
			return state;
		default: return state;
	}
}


// 
const teacher = (state='', action) => {
	switch(action.type){
		case "SET_TEACHER":
			state = action.val;
			return state;
		default: return state;
	}
}

const reducer = combineReducers({
	cla,
	lesson,
	students,
	curStudents,
	defaultCla,
	defaultLesson,
	curProjects,
	setProjects,
	targetStudentProjects,
	targetStudent,
	teacher,
});

export default reducer;


